import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { FdPage } from './fd.page';

const routes: Routes = [
  {
    path: '',
    component: FdPage
  },
  {
    path: 'fdview',
    loadChildren: () => import('./fdview/fdview.module').then( m => m.FdviewPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FdPageRoutingModule {}
